import React, { Component } from "react";
import { message, Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import { session, storage, setCookie, reg, trim, os, removeCookie } from "../../util/util";
import Footer from "../../common/Widget/Footer";
import "./login.scss";
import Perfectinformation from "../../components/Perfectinformation";
import Forgetpwd from "./Forgetpwd";
import Header from "../../components/Header";
import { buyUrl } from '../../util/url';

// 1215456
@withTranslation()
@inject("login", "playlist", "rahmen", "photoalbum")
@observer
class Login extends Component {
  constructor(props) {
    super(props);
    this.userRef = React.createRef();
    this.pwdRef = React.createRef();
    this.t = this.props.t;
    this.state = {
      disabl: false,
      pro_visible: false,
      user_id: null,
      name: null,
      pwd: null,
      for_visible: false,
    };
  }

  componentDidMount() {
    storage.remove("user");
    storage.remove("Z_H_U_DATA");
    session.remove("token");
    removeCookie("cookie");
    this.props.login.clearData();
    this.props.photoalbum.clearData();
    this.props.rahmen.clearData();
    this.props.playlist.clearData();
  }

  handleSubmit = () => {
    const key = "login";
    let name = trim(this.userRef.current.value);
    let pwd = trim(this.pwdRef.current.value);
    if (name === "" || pwd === "") {
      message.error(this.t("AccountPwdEmpty"));
      return;
    }
    if (!reg.pwd.test(pwd)) {
      message.error({ content: this.t("LoginFailed"), key, duration: 3 });
      return;
    }
    message.loading({ content: this.t("loading"), key, duration: 0 });
    this.setState({ disabl: true });
    const data = {
      name,
      pwd,
    };

    this.props.login.login_in(data, (res) => {
      if (res) {
        session.set("token", res.token);
        setCookie("cookie", session.get("token"), 0);
        storage.set("user", { ...res, ...data });
        storage.set("Z_H_U_DATA", res);
        this.setState({ disabl: false });
        message.success({ content: this.t("LoginSucceed"), key, duration: 2 });
        if (this.props.location.state) {
          const path = this.props.location.state.from;
          this.props.history.push(path);
        } else {
          this.props.history.push("/");
        }
      } else {
        console.log("object");
        this.setState({ disabl: false });
        message.error({ content: this.t("LoginFailed"), key, duration: 3 });
      }
    });

    const OS = os();

    if (OS.isAndroid || OS.isPhone) {
      // 手机
      this.props.login.getos("phone");
    } else if (OS.isTablet) {
      // 平板
      this.props.login.getos("tab");
    } else if (OS.isPc) {
      // pc
      this.props.login.getos("pc");
    }
  };

  keyhandleSubmit = (event) => {
    event.preventDefault();
    if (event.keyCode === 13) {
      this.handleSubmit();
    }
  };

  nextinput = (event) => {
    event.preventDefault();
    if (event.keyCode === 13) {
      this.pwdRef.current.focus();
    }
  };

  goRegister = () => {
    this.props.history.push("/register");
  };

  render() {
    const { t } = this.props;
    const { disabl, pro_visible, user_id, name, pwd, for_visible } = this.state;

    return (
      <div className="container">
        <Header buyUrl={buyUrl} title={t('Signsmartframe')} />
        <div className='login-mod'>
          <div className='login-container'>
            <div className='login-box'>
              <div className='login-inner'>
                <div className='login-inner-head'>
                  <h2>{t('login')}</h2>
                </div>
                <div className="login-content">
                  <Row gutter={[0, 16]}>
                    <Col xs={{ span: 24 }} className="login-item-name">
                      <input
                        spellCheck={false}
                        type="text"
                        maxLength={24}
                        placeholder={t("name/mailbox")}
                        ref={this.userRef}
                        onKeyUp={this.nextinput}
                      />
                    </Col>
                    <Col xs={{ span: 24 }} className="login-item-pwd">
                      <input
                        type="password"
                        placeholder={t("Password")}
                        maxLength={32}
                        ref={this.pwdRef}
                        onKeyUp={this.keyhandleSubmit}
                      />
                    </Col>
                    <Col xs={{ span: 24 }}>
                      <p className="login-forgetpwd">
                        <span
                          className="forget-password"
                          onClick={() => {
                            this.setState({ for_visible: true });
                          }}
                        >
                          {t("ForgetPwd")}
                        </span>
                      </p>
                    </Col>
                    <Col xs={{ span: 24 }} className="login-item-loginbtn">
                      <button
                        onClick={this.handleSubmit}
                        style={disabl ? { opacity: "0.5" } : {}}
                        disabled={disabl ? true : false}
                      >
                        {t("login")}
                      </button>
                    </Col>
                    <Col xs={{ span: 24 }} className="login-item-regbtn">
                      <button onClick={this.goRegister}>{t("Registered")}</button>
                    </Col>
                  </Row>
                </div>
              </div>
              <Footer />
            </div>

            <div className="l-top"></div>
            <div className="l-middle"></div>
            <div className="l-bottom"></div>
          </div>
        </div>

        {pro_visible && (
          <Perfectinformation user_id={user_id} name={name} pwd={pwd} />
        )}

        {for_visible && (
          <Forgetpwd
            close={() => {
              this.setState({ for_visible: false });
            }}
          />
        )}
      </div>
    );
  }
}

export default Login;
