import React, { Component } from "react";
import { observer } from "mobx-react";
import { withTranslation } from "react-i18next";
import "./login.scss";
import { Icon, message, Tooltip, Row, Col } from "antd";
import { observable } from "mobx";
import { wsget } from "../../ws";
import { trim, reg } from "../../util/util";

const key = "forgetpwd";

@withTranslation()
@observer
class Forgetpwd extends Component {
  constructor(props) {
    super(props);
    this.textRef = React.createRef();
    this.answerRef = React.createRef();
    this.newpwdtRef = React.createRef();
    this.confirmpwdRef = React.createRef();
    this.t = this.props.t;
  }

  @observable idx = 0;
  @observable name = "";
  @observable question = "";
  @observable answer = "";

  componentDidMount() {
    // this.textRef.current.focus();
  }

  next0 = () => {
    const val = trim(this.textRef.current.value);
    if (!val) {
      message.error(this.t("enterName/mailbox"));
      return;
    }
    if (!reg.name.test(val)) {
      message.error(this.t("isCorrect"));
      return;
    }
    message.loading({ content: this.t("Calibration"), key, duration: 0 });
    wsget("user_back_password", val)
      .then((data) => {
        message.success({ content: this.t("enterAnswer"), key, duration: 2 });
        this.name = data.name;
        this.question = data.pwd_question;
        this.idx = 1;
        this.answerRef.current.focus();
      })
      .catch(() => {
        message.error(this.t("isCorrect"));
      });
  };

  next1 = () => {
    const val = trim(this.answerRef.current.value);
    if (!val) {
      message.error(this.t("enterAnswer"));
      return;
    }
    message.loading({ content: this.t("Calibration"), key, duration: 0 });
    wsget("user_password_answer", { name: this.name, answer: val })
      .then((data) => {
        message.success({ content: this.t("setnewPwd"), key, duration: 2 });
        this.answer = val;
        this.idx = 2;
      })
      .catch((res) => {
        if (res.state === 2) {
          message.error(this.t("WrongAnswer"));
        }
      });
  };

  confirm = () => {
    const pwd1 = this.newpwdtRef.current.value;
    const pwd2 = this.confirmpwdRef.current.value;
    if (!reg.pwd.test(pwd1)) {
      message.error(this.t("pwdFormat"));
      return;
    }
    if (!pwd1 && !pwd2) {
      message.error(this.t("setnewPwd"));
      return;
    }
    if (pwd1 !== pwd2) {
      message.error(this.t("twoPwds"));
      return;
    }
    const data = {
      name: this.name,
      answer: this.answer,
      pwd: pwd2,
    };
    wsget("user_new_password", data, 1).then((res) => {
      this.props.close();
    });
  };

  black1 = () => {
    this.idx = 0;
  };

  black2 = () => {
    this.idx = 1;
  };

  render() {
    const { t } = this.props;
    const { idx } = this;
    const El = () => {
      switch (idx) {
        case 0:
          return (
            <div className="details">
              <Row gutter={[0, 16]}>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <label className="forgetpwd_label" htmlFor="name">
                    {t("name/mailbox")} :
                  </label>
                </Col>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <input
                    id="name"
                    className="forgetpwd_input"
                    type="text"
                    autoFocus="autoFocus"
                    ref={this.textRef}
                    placeholder={t("enterName/mailbox")}
                  />
                </Col>
                <Col xs={{ span: 24 }}>
                  <span className="next" onClick={this.next0}>
                    {t("NextStep")}
                  </span>
                </Col>
                <Col xs={{ span: 0 }}>
                  <span className="black" style={{ visibility: "hidden" }}>
                    {t("PreviousStep")}
                  </span>
                </Col>
              </Row>
            </div>
          );

        case 1:
          return (
            <div className="details">
              <Row gutter={[0, 16]}>
                <Col xs={{ span: 24 }} className="user-name">
                  <p className="title">{t("UserName")}</p>
                  <p className="txt">{this.name}</p>
                </Col>
                <Col xs={{ span: 24 }} className="user-question">
                  <p className="title">{t("Problem")}</p>
                  <p className="txt">{this.question}</p>
                </Col>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <label className="forgetpwd_label" htmlFor="answer">
                    {t("Answer")} :
                  </label>
                </Col>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <input
                    id="answer"
                    className="forgetpwd_input"
                    type="text"
                    autoFocus="autoFocus"
                    ref={this.answerRef}
                    placeholder={t("enterAnswer")}
                  />
                </Col>
                <Col xs={{ span: 24 }}>
                  <span className="next" onClick={this.next1}>
                    {t("NextStep")}
                  </span>
                </Col>
                <Col xs={{ span: 24 }}>
                  <span className="black" onClick={this.black1}>
                    {t("PreviousStep")}
                  </span>
                </Col>
              </Row>
            </div>
          );

        case 2:
          return (
            <div className="details">
              <Row gutter={[0, 16]}>
                <Col xs={{ span: 24 }} className='forgetpwd_item'>
                  <label className="forgetpwd_label" htmlFor="new-pwd">
                    {t("NewPwd")} :
                </label>
                </Col>
                <Col xs={{ span: 24 }} className='forgetpwd_item'>
                  <Tooltip
                    placement="topLeft"
                    overlayClassName="tooltip"
                    trigger="focus"
                    title={t("pwdreg")}
                  >
                    <input
                      id="new-pwd"
                      className="forgetpwd_input"
                      type="password"
                      ref={this.newpwdtRef}
                      placeholder={t("enterNewpwd")}
                    />
                  </Tooltip>
                </Col>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <label className="forgetpwd_label" htmlFor="confirm-pwd">
                    {t("ConfirmPwd")} :
                </label>
                </Col>
                <Col xs={{ span: 24 }} className="forgetpwd_item">
                  <input
                    id="confirm-pwd"
                    className="forgetpwd_input"
                    type="password"
                    ref={this.confirmpwdRef}
                    placeholder={t("enterPwdAgain")}
                  />
                </Col>
                <Col xs={{ span: 24 }}>
                  <span className="next" onClick={this.confirm}>
                    {t("Determine")}
                  </span>
                </Col>
                <Col xs={{ span: 24 }}>
                  <span className="black" onClick={this.black2}>
                    {t("PreviousStep")}
                  </span>
                </Col>
              </Row>
            </div>
          );

        default:
          break;
      }
    };

    return (
      <div className="forgetpwd-model">
        <div className="forgetpwd-inner">
          <div className="forgetpwd-content">
            <div className="forgetpwd-bd">
              <span className="close-icon" onClick={this.props.close}>
                <Icon type="close" />
              </span>
              <div className="forgetpwd-head">
                <span>{t("ResetPwd")}</span>
              </div>
              <El />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Forgetpwd;
